import { connection } from "../store/connection.js";
import { orders, products, itemsData } from "../store/data.js";
import {
  importFinishedCallback,
  encodeToObject,
  loadFromObject
} from "./localstorage.js";
import { saveData, getData } from "./parseapi.js";

const stores = [connection, orders, products, itemsData];

let unsub = [];
export async function enableSync() {
  await load();

  unsub.forEach((u) => u());
  unsub = stores.map((s) => subscribeIgnoreFirst(s, onchange));
}

export async function load() {
  const data = await getData();

  if (!data) {
    return;
  }

  loadFromObject(data);

  importFinishedCallback();
}

const sync = debounce(saveData, 6000);
function onchange() {
  const data = encodeToObject();
  sync(data);
}

function debounce(func, timeout = 1000) {
  let timer;
  return (...args) => {
    clearTimeout(timer);
    timer = setTimeout(() => {
      func(...args);
    }, timeout);
  };
}

function subscribeIgnoreFirst(store, fn) {
  let firedFirst = false;
  return store.subscribe((state) => {
    if (!firedFirst) {
      firedFirst = true;
    } else {
      fn(state);
    }
  });
}
