import Parse from "parse";
import { StorageController } from "./parse/storagectl.js";

Parse.initialize(
  "F4RVv3H34sJa98XgCSNAQUmHMDAXygEg3BPx5m5a",
  "vpARtWXJ2Sh5Eo4vleZozrFNqJBMQXj0tnbj7QNT"
);
Parse.serverURL = "https://parseapi.back4app.com/";

Parse.CoreManager.setStorageController(StorageController);
const OrderData = Parse.Object.extend("OrderData");

export async function loginOrCreateUser(email, password) {
  console.log("Logging in", email);
  try {
    const u = await Parse.User.logIn(email, password);
    return u;
  } catch (err) {
    let user = new Parse.User();
    user.set("username", email);
    user.set("email", email);
    user.set("password", password);
    const u = await user.save();
    return u;
  }
}

export async function currentUser() {
  console.log("Getting current user", await Parse.User.currentAsync());
  return Parse.User.currentAsync();
}

export async function getData(user = Parse.User.current()) {
  console.log("Getting data");
  const q = new Parse.Query(OrderData);
  const f = await q.first();
  if (f) {
    return f.get("data");
  }
}

export async function saveData(orderData, user = Parse.User.current()) {
  console.log("Savedata");
  let data;

  if (user) {
    const q = new Parse.Query(OrderData);
    data = await q.first();
  }

  if (!data) {
    data = new OrderData();
  }
  data.set("data", orderData);
  data.setACL(user);
  data.save();
}
