const prefix = "parse-";

export const StorageController = {
  async: 0,

  getItem(path) {
    console.log(
      "GET",
      prefix + path,
      " --> ",
      localStorage.getItem(prefix + path)
    );
    return localStorage.getItem(prefix + path);
  },

  setItem(path, value) {
    try {
      console.log("SET", prefix + path, value);
      localStorage.setItem(prefix + path, value);
    } catch (e) {
      // Quota exceeded, possibly due to Safari Private Browsing mode
      console.log(e.message);
    }
  },

  removeItem(path) {
    try {
      console.log("REMOVE", prefix + path);
      localStorage.removeItem(prefix + path);
    } catch (e) {
      console.log(e);
    }
  },

  getAllKeys() {
    const keys = [];
    for (let i = 0; i < localStorage.length; i += 1) {
      const key = localStorage.key(i);
      if (key.startsWith(prefix)) {
        keys.push(localStorage.key(i).substring(prefix.length));
      }
    }
    console.log("ALLKEYS", keys);

    return keys;
  },

  clear() {
    console.log("CLEAR");

    const keys = this.getAllKeys();
    keys.forEach((k) => localStorage.removeItem(prefix + k));
  }
};
