<script>
  import {
    importFinishedCallback,
    encodeToObject,
    loadFromObject
  } from "../lib/localstorage.js";
  import { load } from "../lib/sync.js";
  let open = false;
  let files;

  function save() {
    const file = new File(
      [JSON.stringify(encodeToObject(), null, 2)],
      "order-explorer-save.json"
    );

    const link = document.createElement("a");
    link.style.display = "none";
    link.href = URL.createObjectURL(file);
    link.download = file.name;

    // It needs to be added to the DOM so it can be clicked
    document.body.appendChild(link);
    link.click();

    // To make this work on Firefox we need to wait
    // a little while before removing it.
    setTimeout(() => {
      URL.revokeObjectURL(link.href);
      link.parentNode.removeChild(link);
    }, 0);
  }

  function loadFromFile() {
    console.log(files);
    const reader = new FileReader();
    reader.readAsText(files.item(0));
    reader.onload = () => {
      open = false;
      const data = JSON.parse(reader.result);
      loadFromObject(data);
      const date = data.exportDate;
      alert(`Loaded save from ${date}`);
    };
  }

  function toggleOpen() {
    if (!open) {
      open = true;
    } else {
      loadFromFile(files);
    }
  }
</script>

<style>
  .btn {
    border: 0px;
    padding: 0.5rem;
  }
</style>



{#if !open}
  <button class="btn" on:click|preventDefault={save}>Save File</button>
  <button class="btn" on:click|preventDefault={toggleOpen}>Open File</button>
  <button class="btn" on:click|preventDefault={load}> Load from Remote </button>
{:else}
<input type="file" bind:files />
<button class="btn" on:click|preventDefault={toggleOpen}>Open</button>
{/if}