<script>
  import { loginOrCreateUser, currentUser } from "../lib/parseapi.js";
  import { enableSync } from "../lib/sync.js";
  let user = currentUser();
  let email;
  let password;

  user.then(u => u && enableSync());

  async function login() {
    user = await loginOrCreateUser(email, password);
    if (user) {
      console.log("CURRENT USER", user);
      enableSync();
    }
  }
</script>

<div>
  {#await user}
  ...
  {:then u}
    {#if !u}
      <input bind:value={email} type="email" />
      <input bind:value={password} type="password" />
      <button on:click={login}>New User</button>
    {:else}
      Logged In as {u.get("email")}
      <button>Logout</button>
    {/if}
  {/await}
</div>